@use "base/variables" as v;
@use "base/mixins" as m;

.dashboard {
  background-color: v.$grisClaro;

  @include m.tablet {
    display: flex;
    height: 100vh;
  }

  .principal {
    flex: 1;
    overflow: scroll;
  }

  .contenido {
    padding: 2rem;

    .nombre-pagina {
      font-size: 2.8rem;
      text-align: center;
    }
  }

  .formulario {
    input:not([type="submite"]) {
      background-color: v.$blanco;
    }

    input[type="submit"] {
      @include m.boton(v.$indigo, v.$blanco);
      padding: 1rem;
      font-weight: bold;
      font-size: 1.4rem;
      height: 45px;
      padding: 15px 40px;
    }
  }

  .enlace {
    display: block;
    font-weight: bold;
    margin-bottom: 3rem;
    color: v.$indigo;

    &:hover {
      color: darken(v.$indigo, 20%);
    }
  }
}

.listado-tareas {
  padding: 0;
}
